import { _decorator, Collider2D, Component, IPhysics2DContact, Node } from 'cc';
import { Boss } from './Boss';
import { GameCtrl } from './GameCtrl';
const { ccclass, property } = _decorator;

@ccclass('Boss3')
export class Boss3 extends Boss {
    update(dt: number): void {
        super.update(dt);
    }
    start() {
        super.start();
    }
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact){
        super.onBeginContact(selfCollider, otherCollider, contact);
        this.scheduleOnce(() => {this.bulletCont2.push(otherCollider)});
        this.currentHealth2 -= 1;
        if (this.currentHealth2 <= 0) {
            this.currentHealth2 = 0;
        }
        GameCtrl.instance.healthBar.progress = this.currentHealth2 / this.maxHealth2;
        if(this.bulletCont2.length >= 100){
            this.scheduleOnce(() => {GameCtrl.instance.countPoint(1000)});
            GameCtrl.instance.healthBar.node.active = false;  
            this.scheduleOnce(() => {selfCollider.node.destroy()});
            this.maxHealth3 = 150;
            this.currentHealth3 = 150;
            GameCtrl.instance.schedule(GameCtrl.instance.spawnTurn3, 7, 9, 0);
        }

    }
}


